.album {
  border: double 8px rgb(122, 111, 111);
  max-height: 30%;
  max-width: 20%;
  min-width: 20%;
  min-height: 30%;
  margin: 5px;
  text-align: right;
  color: rgb(224, 162, 80);
  background-color: rgb(39, 36, 36);
  padding: 1%;
}
.albumdetails {
  padding: 5%;
  text-align: left;
}
btn-ctn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
btn-ctn button {
  margin-top: 3%;
  height: 33px;
  width: 30%;
  border: 0;
  color: #dbb4b4;
  padding: 8px;
  font-size: 15px;
  border-radius: 3px;
  cursor: pointer;
  text-align: center;
}
#update {
  background-color: rgb(88, 234, 132);

  width: 75px;
}
#update:hover {
  background-color: rgb(44, 91, 107);
  text-decoration: underline;
}
#delete {
  background-color: rgb(222, 99, 99);
}
#delete:hover {
  background-color: rgb(151, 80, 80);
  text-decoration: underline;
}
